import React,{Component} from "react";
import {CSSTransition} from 'react-transition-group'
import { SearchInfoList,SearchInfoItem, SearchInfoSwitch,SearchInfoTitle,SearchInfo,HeaderWrapper,SearchWrapper,Logo,Nav,NavItem,NavSearch,Addition,Button } from "./style";
// connect連接header跟store
import { connect } from "react-redux";
// import {searchFocus,searchBlur} from './store/actioncreators'
import { actionCreators } from "./store/index";
import * as loginaction from '../../pages/login/store/actioncreator'
import {Link} from 'react-router-dom'
class Header extends Component{
     getListArea(){
        const {totalPage,handleChangePage, mouseIn,handleMouseLeave,handleMouseEnter,focused,list,page}=this.props
        const pageList=[]
        const newList=list.toJS()
        if(newList.length){
            for(let i=(page-1)*10;i<page*10;i++){
            pageList.push(
                <SearchInfoItem key={newList[i]}>{newList[i]}</SearchInfoItem>
            )
        }
        }
        
        
        if(focused||mouseIn){
            return(
                <SearchInfo 
                    onMouseEnter={handleMouseEnter}
                    onMouseLeave={handleMouseLeave}>
                                <SearchInfoTitle>
                                    熱門搜索
                                    <SearchInfoSwitch onClick={()=>{handleChangePage(page,totalPage)}}>換一批</SearchInfoSwitch>
                                </SearchInfoTitle>
                                <SearchInfoList>
                                    {pageList}
                                </SearchInfoList>
                            </SearchInfo>
            )
        }else{
            return null
        }
    }
    // constructor(props){
    //     super(props)
        
    //     this.handlefocus=this.handlefocus.bind(this)
    //     this.handleblur=this.handleblur.bind(this)
    // }
    render(){
        const {login,list,focused,handleblur,handlefocus,logout}=this.props
        return(
            <HeaderWrapper>
                <Link to='/'>
                    <Logo></Logo>
                </Link>
                
                <Nav>
                    <NavItem className='left active'>首頁</NavItem>
                    <NavItem className='left'>下載</NavItem>
                    {
                        login
                        
                        ?
                         
                            <NavItem className='right'  onClick={logout}>登出</NavItem>
                        
                        :<Link to='/login'>
                            <NavItem className='right'>登入</NavItem>
                        </Link>
                    }
                    
                    <NavItem className='right'>Aa</NavItem>
                    <SearchWrapper>
                        <CSSTransition
                            in={focused}
                            classNames='slide'
                            timeout={200}
                        >
                            <NavSearch onBlur={handleblur} onFocus={()=>handlefocus(list)} className={focused?'focused':''}>
                            </NavSearch>
                            
                        </CSSTransition>
                        
                        {this.getListArea(this.props.focused)}
                    </SearchWrapper>
                    
                </Nav>
                <Addition>
                    
                    <Link to='/write'>
                    <Button className="writing">寫文章</Button>
                    </Link>
                    
                    <Button className="reg">註冊</Button>
                </Addition>
            </HeaderWrapper>
            
        )
    }
    // handlefocus(){
    //     this.setState({
    //         focused:true
    //     })
    // }
    // handleblur(){
    //     this.setState({
    //         focused:false
    //     })
    // }
}
const mapStateToProps=(state)=>{
    return{
        // 將reducer的focused映射到props
        focused:state.header.get('focused')
       ,list:state.header.get('list'),
       page:state.header.get('page'),
       mouseIn:state.header.get('mouseIn'),
       totalPage:state.header.get('totalPage'),
       login:state.login.get('login')
    }

}
const mapDispatchToProps=(dispatch)=>{
    return{
        handlefocus(list){
            // console.log('123')
            // const action={
            //     type:'search_focus'
            // }
                if(list.size===0){
                    dispatch(actionCreators.getList())
                
                }
               
            dispatch(actionCreators.searchFocus())
            
        },
        handleblur(){
            // console.log('123')
            // const action={
            //     type:'search_blur'
            // }
            dispatch(actionCreators.searchBlur())
        },
        handleMouseEnter(){
            dispatch(actionCreators.mouseEnter())
        },
        handleMouseLeave(){
            dispatch(actionCreators.mouseLeave())
        },
        handleChangePage(page,totalPage){
            console.log(page,totalPage)
            if(page<totalPage){
                dispatch(actionCreators.changePage(page+1))
            }else{
                dispatch(actionCreators.changePage(1))
            }
            
        },
        logout(){
            console.log('log out')
            dispatch(loginaction.logout())
        }
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(Header)