import { fromJS } from "immutable"
import { CHANGE_LOGIN, LOGOUT } from "./constant"
const defaultState=fromJS({
    login:false
})
export default(state=defaultState,action)=>{
    switch(action.type){
        case CHANGE_LOGIN:
            console.log('login')
            return state.set('login',action.value)
        case LOGOUT:
                console.log('logout')
                return state.set('login',action.value) 
        default:
            return state
    }
}