import React, { useEffect, useState } from "react";
import { redirect, useNavigate } from "react-router-dom";
import { connect } from "react-redux";
import { actioncreator } from "./store";
import { useParams } from "react-router-dom";
import {Button, Input, LoginBox, LoginWrapper} from './style'
const Login = (props) => {
    const {handlelogin,login}=props
    const [account,setAccount]=useState()
    const [password,setPasswor]=useState()
    const navigate=useNavigate()
    useEffect(()=>{
        if(login){
            navigate('/')
        }
    },[login,navigate])
    if(!login){
        return(
        <LoginWrapper>
            <LoginBox>
               <Input  value={account}placeholder="帳號" onChange={(e)=>{setAccount(e.target.value)}}></Input>
                <Input value={password} type="password" onChange={(e)=>{setPasswor(e.target.value)}} placeholder="密碼"></Input>
                <Button onClick={()=>handlelogin(account,password)}>登入</Button>
            </LoginBox>
            
        </LoginWrapper>
    )
    }else{
        return null
    }
    
};

const mapState = (state) => ({
  login:state.login.get('login')
});

const mapDispatch = (dispatch) => ({
    handlelogin(account,password){
        dispatch(actioncreator.login(account,password))
        console.log(account,password)
    }
});

export default connect(mapState, mapDispatch)(Login);