import React,{PureComponent} from "react";
import {HomeWrapper,HomeLeft,HomeRight} from './style'
import Topic from "./components/topic";
import List from "./components/list";
import Recommend from "./components/recommend";
import Writer from "./components/writer";
import { actionCreators } from "./store";
import { connect } from "react-redux";
import {BackTop} from './style'
class Home extends PureComponent{
    handlescrolltop(){
        window.scrollTo(0,0)
    }
    render(){
        return(
            <HomeWrapper>
                <HomeLeft>
                    <img className="banner-img" src="https://static.vecteezy.com/system/resources/previews/000/664/751/original/vector-abstract-low-poly-banner-design.jpg"></img>
                    <Topic></Topic>
                    <List></List>
                </HomeLeft>
                <HomeRight>
                    <Recommend></Recommend>
                    <Writer></Writer>
                </HomeRight>
                {
                this.props.showScroll ?<BackTop onClick={this.handlescrolltop}>回到頂部</BackTop>:null
                }
                
            </HomeWrapper>
        )
    }

    componentDidMount(){
        this.props.changeHomeData() 
        this.bindEvents()
    }
    
    componentWillUnmount(){
        window.removeEventListener('scroll',this.props.changescrolltopshow)
    }
    bindEvents(){
        window.addEventListener('scroll',this.props.changescrolltopshow)
    }    
}
       

const mapState=(state)=>({
    showScroll:state.home.get('showScroll')
})
const mapDispatch=(dispatch)=>({
    changeHomeData(){
        const action=actionCreators.getHomeInfo()
        dispatch(action)
    },
    changescrolltopshow(){
        if(document.documentElement.scrollTop>100){
            dispatch(actionCreators.toggletopshow(true))
        }else{
            dispatch(actionCreators.toggletopshow(false))
        }
    }
})
export default connect(mapState,mapDispatch) (Home)