import {fromJS} from 'immutable'
import { GETDETAIL } from './actiontype'

const defaultState=fromJS({
    title:'',
    content:''
})
export default(state=defaultState,action)=>{
    switch(action.type){
        case GETDETAIL:
            return state.merge({
                title:fromJS(action.title),
                content:fromJS(action.content)
            })
        default:
            return state
    }
}