import React,{Component} from "react";
import {Recommend_container,Hot,Qr,Hot_desc} from '../style'
import { connect } from "react-redux";
class Recommend extends Component{
    render(){
        const {Recommend_item}=this.props
        return(
            
            
            <Recommend_container>
                    {
                        Recommend_item.map((item)=>{
                        return(
                            <Hot key={item.get('id')} imgurl={item.get('url')}>
                                <Hot_desc>{item.get('title')}</Hot_desc>
                            </Hot>
                            )
                            
                         })
                            }
                <Qr></Qr>
            </Recommend_container>
        )
    }
}
const MapState=(state)=>({
    Recommend_item:state.home.get("Recommend")
})

export default connect(MapState,null)(Recommend)