import styled from "styled-components";
export const HomeWrapper=styled.div`
    width:960px;
    margin:0 auto;
    overflow:hidden;
`
export const HomeLeft=styled.div`
    width:625px;
    margin-left:15px;
    padding-top:30px;
    float:left;
    .banner-img{
        width:625px;
        height:270px;
    }
`
export const HomeRight=styled.div`
    width:280px;
    float:right;
   
`
export const TopicWrapper=styled.div`
    overflow:hidden;
    padding:20px 0 10px 0;
    margin-left:-18px;
    border-bottom:1px solid #dcdcdc

`
export const TopicItem=styled.div`
    float:left;
    height:32px;
    line-height:32px;
    margin-left:18px;
    font-size:14px;
    color:#000;
    border-radius:4px;
    border: 1px solid #dcdcdc;
    background:#f7f7f7;
    padding-right:10px;
    margin-bottom:18px;
    .topic-pic{
        display:block;
        float:left;
        width:32px;
        height:32px;
        margin-right:10px
    }
`
export const ListItem=styled.div`
    overflow:hidden;
    padding:20px 0;
    border-bottom:1px solid #dcdcdc;
    .pic{
        display:block;
        width:125px;
        height:100px;
        float:right;
        border-radius:10px;
    }
    `
export const ListInfo=styled.div`
    width:500px;
    float:left;
    .title{
        font-size:18px;
        line-height:27px;
        font-weight:bold;
        color:#333;
    }
    .desc{
        line-height:18px;
        font-size:13px;
        color:#999;
    }
`
export const Recommend_container=styled.div`
    
    margin-top:30px;
`
export const Hot=styled.div`
    width:280px;
    height:50px;
    
    border-radius:5px;
    margin:10px 0;
    background-image: url(${(props) => props.imgurl});
    background-size: cover; /* or background-size: contain; depending on your design */
    background-repeat: no-repeat;
    background-position: center;
`
export const Hot_desc=styled.p`
    margin:10px 20px;
    position:relative;
    top:15px;
`
export const Qr=styled.div`

`
export const LoadMore=styled.div`
    width:100%;
    height:40px;
    line-height:40px;
    background:#a5a5a5;
    text-align:center;
    border-radius:20px;
    color:#fff;
    margin:30px 0;
    cursor:pointer;
`
export const BackTop=styled.div`
    position:fixed;
    right:50px;
    bottom:30px;
    width:60px;
    height:60px;
    line-height:60px;
    text-align:center;
    border:1px solid #ccc;
    font-size:14px;
`