import styled from "styled-components";
import logoPic from'../img/nav-logo-4c7bbafe27adc892f3046e6978459bac.png'
export const HeaderWrapper=styled.div`
    height:56px;
    position:relative;
    border-bottom:1px solid #f0f0f0;
    z-index:1;
`
export const Logo=styled.div`
height:56px;
position:absolute;
top:0;
left:0;
width:100px;
display:block;
background:url(${logoPic});
background-size:contain
`
export const Nav=styled.div`
width:960px;
margin:0 auto;
height:100%;
padding-right:70px;
box-sizing:border-box;
`
export const NavItem=styled.div`
line-height:56px;
padding:0 15px;
font-size:17px;
color:#333;
    &.left{
        float:left;
    }
    &.right{
        float:right;
        color:#969696
    }
    &.active{
        color:#ea6f5a;
    }

`
export const NavSearch=styled.input.attrs({
    placeholder:"搜索"})
`   margin-top:9px;
    padding:0 30px 0 20px;
    box-sizing:border-box;
    width:160px;
    height:39px;
    border:none;
    border-radius:19px;
    background:#eee;
    font-size:14px;
    margin-left:20px;
    color:#666;
    &::placeholder{
        color:#999;
    }
    &.focused{
        width:240px;
    }
    &.slide-enter{
        transition:all .2s ease-out;
        width:160px;
    }
    &.slide-enter-active{
        width:240px;
    }
    &.slide-exit{
        transition:all .2s ease-out;
    }
    &.slide-exit-active{
        width:160px;
    }
    `


    export const Addition=styled.div`
    position:absolute;
    right:0;
    top:0;
    height:56px;
`
export const Button= styled.div`
float:right;
line-height:38px;
border-radius:19px;
margin-top:9px;
border:1px solid #ec6149;
margin-right:20px;
padding:0 20px;
&.reg{
    color:#ec6149;
}
&.writing{
    color:#fff;
    background:#ec6149;
}
`
export const SearchWrapper=styled.div`
    float:left;
    position:relative;
    
`
export const SearchInfo=styled.div`
    position:absolute;
    background:#fff;
    left:0;
    top:56px;
    width:240px;
    padding:0 20px;
    box-shadow:0 0 8px rgba(0,0,0,.2);
    
`
export const SearchInfoTitle=styled.div`
    margin-top:20px;
    margin-bottom:15px;
    line-height:20px;
    font-size:14px;
    color:#969696;
`
export const SearchInfoSwitch=styled.span`
    float:right;
    font-size:13px;
`
export const SearchInfoItem=styled.a`
    display:block;
    float:left;
    font-size:12px;
    line-height:20px;
    padding:0 5px;
    border: 1px solid #ddd;
    color:#787878;
    border-radius:3px;
    margin-right:10px;
    margin-bottom:15px;
`
export const SearchInfoList =styled.div`
    overflow:hidden;
`