import axios from "axios"
import {fromJS} from 'immutable'
import * as constants from './constants'
const changeHomeData=(results)=>({
    type:constants.CHANGE_HOME_DATA,
    topicList:results.topicList,
    articleList:results.articleList,
    Recommend:results.Recommend
})
const addHomeList=(result,nextPage)=>({
    type:constants.ADD_ARTICLE_LIST,
    result:fromJS(result),
    nextPage
})
export const getHomeInfo=()=>{
    return(dispatch)=>{
        axios.get('/api/home.json').then((res)=>{
            let results=res.data.data
            const action=changeHomeData(results)
            dispatch(action)
        })
    }
}
export const getmorelist=(page)=>{
    return(dispatch)=>{
        axios.get(`/api/homeList${page}.json?page=`+page).then((res)=>{
            const result=res.data.data
            console.log(page)
            dispatch(addHomeList(result,page+1))
        })
    }
}
export const toggletopshow=(show)=>({
    type:constants.TOGGLE_SCROLL_TOP,
    show
})